<template>
  <div class="admin">
    <v-container>
      <v-row>
        <v-col>
          <h1 class="text-center my-10">관리자 페이지</h1>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="12">
          <v-card width="100%" class="mx-auto mb-10">
            <v-card-title class="headline text-center my-4 font-weight-bold">관리자 기본정보</v-card-title>
            <v-card-text>
              <v-list>
                <v-list-item-content>
                    <v-list-item-title><strong>이 름 : </strong> {{ userName }}</v-list-item-title>
                    <v-list-item-subtitle class="my-5"><strong>이메일 : </strong> {{ userEmail }}</v-list-item-subtitle>
                    <v-list-item-title class="mb-7"><strong>성경공부 그룹명 : </strong> {{ studyGroupName }}</v-list-item-title>
                    <p class="text-sm text-muted font-weight-bold" style="color: red;">* 비밀번호와 성경공부그룹명은 관리자만 수정할 수 있습니다! 이메일은 변경하실 수 없으니 주의해주세요.</p>
                  </v-list-item-content>
              </v-list>
            </v-card-text>
          </v-card>
          <v-card width="100%" class="mx-auto">
            <v-card-title class="headline text-center my-4 font-weight-bold">관리자 정보수정</v-card-title>
            <v-card-text>
              <v-form>
                <v-text-field
                  label="현재 비밀번호"
                  v-model="currentPassword"
                  prepend-inner-icon="mdi-lock-outline"
                  variant="outlined"
                  type="password"
                  required
                ></v-text-field>
                <v-text-field
                  label="새 비밀번호"
                  v-model="newPassword"
                  prepend-inner-icon="mdi-lock-outline"
                  variant="outlined"
                  type="password"
                  required
                ></v-text-field>
                <div class="text-center mt-7 mb-3">
                  <v-btn
                    :disabled="!valid"
                    color="primary"
                    class="mr-4"
                    @click="updateProfile"
                  >
                    업데이트
                  </v-btn>
                  <v-btn
                    color="info"
                    @click="reset"
                  >
                    초기화
                  </v-btn>
                </div>
              </v-form>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="12" class="rounded-lg" variant="outlined">
          <v-card width="100%" class="mx-auto pa-0">
            <v-card-title class="text-center my-4 font-weight-bold">유저 목록</v-card-title>
            <v-card-text>
              <v-list lines="two">
                <v-list-item
                  v-for="user in users"
                  :key="user.id"
                >
                  <template v-slot:prepend>
                    <v-avatar color="amber">
                      <v-icon color="white">mdi-account</v-icon>
                    </v-avatar>
                  </template>
                  <template v-slot:default>
                    <v-list-item-content class="d-flex flex-column">
                      <v-list-item-title class="mr-10 font-weight-bold">{{ user.data.name }}</v-list-item-title>
                      <div class="d-flex user-align">
                        <v-list-item-subtitle class="hidden-sm-and-down hidden-md-and-down" style="font-size: 16px; width: 250px;">{{ user.data.email }}</v-list-item-subtitle>
                        <input v-model="user.data.password" class="editable" height="auto" style="width: 100px;">
                        <input v-model="user.data.group" class="editable" style="width: 100%;" height="auto">
                      </div>
                    </v-list-item-content>
                  </template>
                  <template v-slot:append>
                    <v-list-item-action class="d-flex flex-grow-1">
                      <v-btn
                        size="x-small"
                        color="primary"
                        icon="mdi-pencil"
                        @click.stop="editUser(user.id, user.data.password, user.data.group)"
                      ></v-btn>
                      <v-btn
                        class="mx-3"
                        size="x-small"
                        color="error"
                        icon="mdi-delete"
                        @click.stop="deleteUser(user.id)"
                      ></v-btn>
                      <v-btn
                        size="x-small"
                        color="grey"
                        icon="mdi-cancel"
                        @click.stop="cancelEdit(user.id)"
                        style="display: none;"
                      ></v-btn>
                    </v-list-item-action>
                  </template>
                </v-list-item>
              </v-list>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { auth } from '@/firebase';
import { ref, computed, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { updatePassword, reauthenticateWithCredential, EmailAuthProvider } from 'firebase/auth';
import { getFirestore, collection, addDoc, getDocs, doc, deleteDoc, updateDoc, getDoc } from 'firebase/firestore'; 

export default {
  name: 'AdminView',
  setup() {
    // 반응형 참조 변수들을 선언합니다.
    const newGroupName = ref('');
    const users = ref([]);
    const editingUserId = ref(null);
    const isAdminEmail = ref(false);
    const newPassword = ref('');
    const currentPassword = ref('');
    const userName = ref('');
    const userEmail = ref('');    
    const studyGroupName = ref('');
    const router = useRouter(); // 라우터 인스턴스를 가져옵니다.
    const db = getFirestore(); // Firestore 인스턴스를 가져옵니다.

    // 현재 로그인한 사용자의 데이터를 가져오는 함수
    const fetchUserData = async () => {
      if (auth.currentUser) {
        const userDoc = await getDoc(doc(db, 'users', auth.currentUser.uid));
        if (userDoc.exists()) {
          const userData = userDoc.data();
          userName.value = userData.name;
          userEmail.value = userData.email;
          studyGroupName.value = userData.group;
          isAdminEmail.value = userData.role === 'admin';
        } else {
          console.log('No such document!');
        }
      }
    };

    // 사용자 프로필을 업데이트하는 함수
    const updateProfile = async () => {
      try {
        if (auth.currentUser) {
          const credential = EmailAuthProvider.credential(auth.currentUser.email, currentPassword.value);
          await reauthenticateWithCredential(auth.currentUser, credential);
          await updatePassword(auth.currentUser, newPassword.value);
          const userRef = doc(db, 'users', auth.currentUser.uid);
          await updateDoc(userRef, {
            password: newPassword.value,
            group: newGroupName.value
          });
          alert('프로필 업데이트 성공!');
          alert('비밀번호와 그룹명이 변경되었습니다. 다시 로그인해주세요.');
          await auth.signOut();
          router.push({ name: 'login' });
        }
      } catch (error) {
        alert(`업데이트 실패: ${error.message}`);
      }
    };

    // 모든 사용자 데이터를 가져오는 함수
    const fetchUsers = async () => {
      const usersRef = collection(db, 'users');
      const querySnapshot = await getDocs(usersRef);
      users.value = querySnapshot.docs.map(doc => ({ id: doc.id, data: doc.data() }));
    };

    // 새 그룹 이름이 유효한지 확인하는 계산된 속성
    const valid = computed(() => {
      return newGroupName.value.trim().length > 0;
    });

    // 새로운 스터디 그룹을 추가하는 함수
    const addStudyGroup = async () => {
      if (!isAdminEmail.value) {
        alert('관리자 권한이 필요합니다.');
        return;
      }
      try {
        const usersRef = collection(db, 'users');
        await addDoc(usersRef, { group: newGroupName.value });
        alert('성경공부 그룹이 추가되었습니다!');
        newGroupName.value = '';
      } catch (error) {
        alert(`그룹 추가 실패: ${error.message}`);
      }
    };

    // 입력 필드를 초기화하는 함수
    const reset = () => {
      newGroupName.value = '';
    };

    const editUser = async (userId, password, group) => {
      if (!isAdminEmail.value) {
        alert('관리자 권한이 필요합니다.');
        return;
      }
      try {
        const userRef = doc(db, 'users', userId);
        await updateDoc(userRef, {
          password: password,
          group: group
        });
        await fetchUsers();
        alert('사용자 정보가 업데이트 되었습니다.');
      } catch (error) {
        alert(`사용자 정보 업데이트 실패: ${error.message}`);
      }
    };

    // 사용자를 삭제하는 함수
    const deleteUser = async (userId) => {
      if (!isAdminEmail.value) {
        alert('관리자 권한이 필요합니다.');
        return;
      }
      try {
        await deleteDoc(doc(db, 'users', userId));
        await fetchUsers();
        alert('유저가 삭제되었습니다!');
      } catch (error) {
        alert(`유저 삭제 실패: ${error.message}`);
      }
    };

    // 편집 모드를 취소하는 함수
    const cancelEdit = (userId) => {
      if (editingUserId.value === userId) {
        editingUserId.value = null;
      }
    };

    // 컴포넌트가 마운트될 때 실행되는 함수
    onMounted(async () => {
      await fetchUserData();
      if (isAdminEmail.value) {
        await fetchUsers();
      }
    });

    // setup 함수에서 반환하는 객체
    return { newGroupName, valid, addStudyGroup, reset, isAdminEmail, users, editUser, deleteUser, cancelEdit, editingUserId, fetchUserData, updateProfile, userName, userEmail, studyGroupName };
  }
};
</script>

<style scoped lang="scss">  
  .user-align {
    font-size: 16px;   
  }

  @media (max-width: 768px) {
    .user-align {
      flex-direction: column;      
    }
  }
</style>
