<template>
  <div class="user">
    <v-container>
      <v-row>
        <v-col>
          <h1 class="text-center my-10">회원 페이지</h1>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="12">
          <v-card width="75%" class="mx-auto mb-10">
            <v-card-title class="headline text-center my-4 font-weight-bold">회원 기본정보</v-card-title>
            <v-card-text>
              <v-list>
                <v-list-item-content>
                    <v-list-item-title><strong>이 름 : </strong> {{ userName }}</v-list-item-title>
                    <v-list-item-subtitle class="my-5"><strong>이메일 : </strong> {{ userEmail }}</v-list-item-subtitle>
                    <v-list-item-title class="mb-7"><strong>성경공부 그룹명 : </strong> {{ studyGroupName }}</v-list-item-title>
                    <p class="text-sm text-muted font-weight-bold" style="color: red;">* 비밀번호는 개인정보 차원에서 제공하지 않습니다!</p>
                  </v-list-item-content>
              </v-list>
            </v-card-text>
          </v-card>
          <v-card width="75%" class="mx-auto">
            <v-card-title class="headline text-center my-4 font-weight-bold">회원 정보수정</v-card-title>
            <v-card-text>
              <v-form>
                <v-text-field
                  label="현재 비밀번호"
                  v-model="currentPassword"
                  prepend-inner-icon="mdi-lock-outline"
                  variant="outlined"
                  type="password"
                  required
                ></v-text-field>
                <v-text-field
                  label="새 비밀번호"
                  v-model="newPassword"
                  prepend-inner-icon="mdi-lock-outline"
                  variant="outlined"
                  type="password"
                  required
                ></v-text-field>
                <div class="text-center mt-7 mb-3">
                  <v-btn
                    :disabled="!valid"
                    color="primary"
                    class="mr-4"
                    @click="updateProfile"
                  >
                    업데이트
                  </v-btn>
                  <v-btn
                    color="info"
                    @click="reset"
                  >
                    초기화
                  </v-btn>
                </div>
              </v-form>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { auth } from '@/firebase'
import { ref, computed, onMounted } from 'vue';
import { useRouter } from 'vue-router'; // 수정된 부분: vue-router에서 useRouter를 가져옵니다.
import { updatePassword, reauthenticateWithCredential, EmailAuthProvider } from 'firebase/auth';
import { getFirestore, doc, getDoc, updateDoc } from 'firebase/firestore';

export default {
  name: 'UserView',
  setup() {
    const newPassword = ref('');
    const currentPassword = ref('');
    const userName = ref('');
    const userEmail = ref('');    
    const studyGroupName = ref('');
    const router = useRouter(); // 수정된 부분: vue-router의 useRouter를 사용합니다.
    const db = getFirestore();

    const valid = computed(() => {
      return newPassword.value.length >= 6;
    });

    // 회원 정보 가져오기
    const fetchUserData = async () => {
      if (auth.currentUser) {
        const userDoc = await getDoc(doc(db, 'users', auth.currentUser.uid));
        if (userDoc.exists()) {
          const userData = userDoc.data();
          userName.value = userData.name;
          userEmail.value = userData.email;
          studyGroupName.value = userData.group;
        } else {
          console.log('No such document!');
        }
      }
    };

    const updateProfile = async () => {
      try {
        if (auth.currentUser) {
          // 현재 사용자의 인증 자격 증명을 생성
          const credential = EmailAuthProvider.credential(auth.currentUser.email, currentPassword.value);

          // 현재 사용자 재인증
          await reauthenticateWithCredential(auth.currentUser, credential);

          // 비밀번호 업데이트
          await updatePassword(auth.currentUser, newPassword.value);

          // 'users' 그룹에서 해당 사용자의 'password' 업데이트
          const userRef = doc(db, 'users', auth.currentUser.uid);
          await updateDoc(userRef, {
            password: newPassword.value
          });
          
          alert('프로필 업데이트 성공!');
          
          // 사용자에게 재로그인하라는 메시지 표시
          alert('비밀번호가 변경되었습니다. 다시 로그인해주세요.');
          
          // 로그아웃
          await auth.signOut();
          
          // 로그인 페이지로 리다이렉트
          router.push({ name: 'login' });
        }
      } catch (error) {
        alert(`업데이트 실패: ${error.message}`);
      }
    };

    const reset = () => {
      newPassword.value = '';
      currentPassword.value = '';
    };

    // onMounted 시키기
    onMounted(() => {
      fetchUserData();
    });

    return { newPassword, currentPassword, valid, userName, userEmail, studyGroupName, updateProfile, reset };
  }
}
</script>

<style scoped lang="scss">
.user .v-card{
  margin-bottom: 100px !important;
}
</style>
