<template>
  <component :is="layout">
    <router-view></router-view>
  </component>  
</template>

<script>
import { auth, db } from '@/firebase';
import { ref, onMounted, onBeforeUnmount } from 'vue';
import { useRouter } from 'vue-router';
import { onAuthStateChanged, signOut } from 'firebase/auth';
import { doc, getDoc } from 'firebase/firestore';
import DefaultLayout from '@/layouts/DefaultLayout'
import IntroLayout from '@/layouts/IntroLayout'

export default {
  name: 'App',
  data: () => ({ 
    open: [],    
  }),
  computed: {
    layout() {
      return (this.$route.meta && this.$route.meta.layout) || 'DefaultLayout'
    }
  },
  components: {
    DefaultLayout,
    IntroLayout
  },
  setup() {
    const router = useRouter();
    const user = ref(null);
    const isLoggedIn = ref(false);
    const drawer = ref(false);
    const role = ref('');

    const links = ref([
      { name: '홈', path: '/home',  icon: 'mdi-home' },
      { name: '성경공부검색', path: '/about', icon: 'mdi-account' },
      { name: '성경공부등록', path: '/study', icon: 'mdi-book-open' },
      { name: '검색', path: '/search', icon: 'mdi-magnify' },
      { name: '이용안내', path: '/manual', icon: 'mdi-help' },
    ]);
    const menus = ref([
      { name: '홈', path: '/home', active: false },
      { name: '성경공부검색', path: '/about', active: false },
      { name: '성경공부등록', path: '/study', active: false },
      { name: '검색', path: '/search', active: false },
      { name: '이용안내', path: '/manual', active: false },
    ]);

    /**
     * Firebase 인증 상태 변화를 모니터링하고 관리하는 함수
     *
     * 1. Firebase 인증 상태 변화 리스너를 등록하여 사용자의 로그인/로그아웃 상태를 추적합니다.
     * 2. 사용자가 로그인하면 `user.value`에 사용자 정보를 저장하고 `isLoggedIn.value`를 true로 설정합니다.
     * 3. 사용자가 로그아웃하면 `user.value`를 null로 설정하고 `isLoggedIn.value`를 false로 설정합니다.
     * 4. 컴포넌트가 언마운트될 때 인증 상태 변화 리스너를 해제합니다.
     */

    const fetchUserDetails = async (uid) => {
      const userDoc = await getDoc(doc(db, 'users', uid));
      if (userDoc.exists()) {
        const userData = userDoc.data();
        role.value = userData.role; // 사용자 역할
        user.value.name = userData.name; // 사용자 이름
      } else {
        role.value = 'user'; // 기본 역할 설정
        user.value.name = '익명'; // 기본 이름 설정
      }
    };


    const initFirebaseAuth = () => {
      const unsubscribe = onAuthStateChanged(auth, async (firebaseUser) => {
        if (firebaseUser) {
          user.value = firebaseUser;
          isLoggedIn.value = true;
          await fetchUserDetails(firebaseUser.uid);
        } else {
          user.value = null;
          isLoggedIn.value = false;
          role.value = null;
        }
      });

      onBeforeUnmount(unsubscribe);
    };

    // 로그아웃 함수: auth 인스턴스 사용
    /**
     * 사용자의 로그아웃을 처리하는 함수
     *
     * 1. Firebase 인증 서비스를 사용하여 사용자를 로그아웃 처리합니다.
     * 2. 로그아웃 성공 시 `user.value`를 null로 설정하고 `isLoggedIn.value`를 false로 설정합니다.
     * 3. 로그아웃 성공 후 '/home' 경로로 라우팅합니다.
     * 4. 로그아웃 실패 시 오류 메시지를 콘솔에 출력합니다.
     */
    const handleLogout = async () => {
      try {
        await signOut(auth);
        user.value = null;
        isLoggedIn.value = false;
        router.push('/');
      } catch (error) {
        console.error('로그아웃 실패:', error);
      }
    };

    onMounted(() => {
      initFirebaseAuth();
    });

    return { drawer, links, menus, isLoggedIn, user, role, initFirebaseAuth, handleLogout };
  }
}
</script>

<style>
/* 전체 스타일 적용 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* App */
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;  
  height: 100vh;
}
</style>
